import React from "react";
import Modal from "../../compoments/Atoms/Modal";
import { ServicesModel } from "../../models/services.model";
import { useChatbot } from "../../Providers/ChatbotContext";
import { usePages } from "../../Providers/PagesContext";
import { useService } from "../../Providers/ServiceContext";
import { ServicePageWrapper } from "./styles";

interface IServicePage {
  show: boolean;
  service?: ServicesModel;
  onBack: () => void;
}

const ServicePage: React.FC<IServicePage> = ({ show, service, onBack }) => {
  const { changePage, Pages } = usePages();
  const { setModalVisible } = useChatbot();
  const { setCurrentService } = useService();
  return (
    <Modal show={show}>
      {!!service && (
        <ServicePageWrapper>
          <div className="head">
            <img
              onClick={() => onBack()}
              className="voltar"
              alt="Voltar"
              src={require("../../assets/voltar.svg").default}
            />
            <img
              alt={service.title}
              src={require("../../assets/services/document.svg").default}
              height={56}
            />
            <h1>{service.title}</h1>
            <div className="button-container">
              <button
                type="button"
                className="ias-button"
                onClick={() => {
                  setCurrentService(undefined);
                  changePage(Pages.ASSISTENCE);
                  setModalVisible(true);
                }}
              >
                Pedir ajuda da IAS
              </button>
              <button type="button" className="service-button">
                Acessar Servio
              </button>
            </div>
          </div>
          <div className="body">
            <img
              src={require("../../assets/processive-steps.svg").default}
              alt="Barra de progresso - Passo 1"
            />
            <h1>Agendamento eletrnico</h1>
            <ul>
              <li>
                O Agendamento de Carteira de Identidade ser realizado atravs
                da plataforma digital:www.vaptvupt.go.gov.br/agendamento
              </li>
              <li>
                O agendamento ser possvel para 1 VIA e para as 2 VIA que
                possuam cadastro no sistema Gois Biomtrico.
              </li>
              <li>
                Em alguns casos de 2 VIA, o requerente ser notificado 
                comparecer a uma unidade de atendimento.
              </li>
              <li>
                O requerente dever preencher um pr-cadastro atravs do site
                citado acima, e aps, agendar local e horrio conforme sua
                disponibilidade.
              </li>
              <li>Importante anotar o nmero de protocolo.</li>
              <li>
                Os itens na cor amarela devero ser preenchidos
                obrigatoriamente.
              </li>
              <li>
                Caso o requerente no possua e-mail, dever criar um e-mail
                antes de realizar o agendamento,pois  um tem de preenchimento
                obrigatrio, uma vez que as informaes do agendamento sero
                enviadas para o e-mail cadastrado.
              </li>
            </ul>
          </div>
        </ServicePageWrapper>
      )}
    </Modal>
  );
};

export default ServicePage;
